import React, { Component } from "react";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import  AddTodo from './AddTodo';

class TodoList extends Component{
    state = {
        todos:[
            {Id: '1', Title:'Do work well always', Status:'Done'},
            {Id: '2', Title:'Be strong at all times', Status:'Pending'}
        ]
    };

    deleteTodos=(todo)=>{
        const filteredItems = this.state.todos.filter(x=> x.Id !== todo.Id)
        this.setState({
            todos: filteredItems 
        })

    };
    addTodo=(todo)=>{
        this.setState({
            todos:[...this.state.todos, todo]
        });

    };

    editTodos=(x)=>{
        this.setState(state => ({
            todos: state.todos.map(todo =>{
                if(todo.Id === x.Id){
                    return{
                         ...todo,
                         Status: todo.Status ==="Done" ? "Pending" : "Done"
                    };
                }else{
                    return todo;
                }
            })

        }));
    };
 
    render(){
        return(

            <div>
                <AddTodo onAdd={this.addTodo}></AddTodo>
                <p>TODO LISTS BY MATT</p>
                <table className="table">
                    <thead>
                        <tr>
                            <th>ID</th>
                            <th>Title</th>
                            <th>Status</th>
                            <th>Pending </th>  
                            <th></th>                    
                            
                        </tr>
                    </thead>
                    <tbody>
                        {this.state.todos.map(x=>{
                            return(
                                <tr>
                                    <td>{x.Id}</td>
                                    <td>{x.Title}</td>
                                    <td style={{color: x.Status ==="Done"? "green": "red"}}>{x.Status}</td>
                                    <td>
                                        <button  className="btn btn-primary" onClick={()=> this.deleteTodos(x)}> 
                                            <span>
                                                <FontAwesomeIcon icon="trash"></FontAwesomeIcon>
                                            </span>
                                        </button>
                                        |
                                        <button className="btn btn-primary" onClick={()=> this.editTodos(x)}>
                                            <span>
                                                <FontAwesomeIcon icon="edit"></FontAwesomeIcon>
                                            </span>
                                        </button>
                                    </td>
                                </tr>

                              );
                        })}
                    </tbody>
                </table>
            </div>
        );
    }

}
export default TodoList;