import React, {Component}from "react";

class Sample extends Component{
    state = {
        colour:'blue',
        age:10
    };
    handleButton =()=>{
        console.log("button clicked");
        /* cant be used directly
        this.state.colour = 'red';
        this.state.age = 20;
        */
       this.setState({
        colour:'red',
        age:20          
       }
       );
    };
    render(){
        return(
         <div>
             <p>{this.state.colour}</p>
             <p>{this.state.age}</p>
             <button type="button" onClick={this.handleButton}>Click Me</button>
         </div>
        );
    }
}
export default Sample;