import React, {Component} from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

class AddTodo extends Component {

    state = {
       Id:"",
       Title:"",
       Status:"Pending"

    };
    handleIdChange=(event)=>{
        console.log(event.target.value);
        this.setState({
            Id:event.target.value
        });

    };
    handleTitleChange=(event)=>{
        console.log(event.target.value);
        this.setState({
            Title:event.target.value
        });
    };

    handleStatusChange=(event)=>{
        console.log(event.target.value);
        this.setState({
            Status:event.target.value
        });
    };

    handleTodoSubmit=(event)=>{
        event.preventDefault();
        //console.log(this.state.Title);
        //console.log(this.state.Status);
        this.props.onAdd({
            Id: this.state.Id,
            Title: this.state.Title,
            Status:this.state.Status
        });
        this.setState({
            Id:"",
            Title:"",
            Status:"Pending"
        }

        );
        

    };
    
    render(){

        return(
         <div>
             <h3>
                 Add Todo
             </h3>
             <form onSubmit={this.handleTodoSubmit}> 
                 <div className="form-group">
                    <input value={this.state.Id} onChange={this.handleIdChange} className="form-control" placeholder="Enter Id"/>
                 </div>
                 <div className="form-group">
                    <input value={this.state.Title} onChange={this.handleTitleChange} className="form-control" placeholder="Enter Title"/>
                 </div>
                 <div className="form-group">
                    <select value={this.state.Status} className="form-control" onChange={this.handleStatusChange}> 
                      <option value="Done">Done</option>
                      <option value="Pending">Pending</option>
                    </select>
                 </div>
                 
                 <button className="form-control btn btn-primary" type="submit"> 
                     Add Todo
                 </button>
                

             </form>
         </div>
        );
    }

};
export default AddTodo;